import { RPTooltip } from "@rp/react/web/common/base-components"
import { Box, IconButton } from "@rp/react/web/mui/core"
import { Download } from "@rp/react/web/mui/icons"

type Props = {
  onDownload: () => void
  disabled?: boolean
  tooltip?: string
}

function WatchlistDataTabsDownloadCsv({ onDownload, disabled, tooltip = "Export to CSV" }: Props) {
  return (
    <RPTooltip title={tooltip} placement="bottom">
      <Box component="span">
        <IconButton
          disabled={disabled}
          size="small"
          onClick={e => {
            e.stopPropagation()
            e.preventDefault()
            onDownload()
          }}
        >
          <Download sx={{ fontSize: 16 }} />
        </IconButton>
      </Box>
    </RPTooltip>
  )
}

export { WatchlistDataTabsDownloadCsv }
